import { map, min, max } from 'lodash'

const lookerTransform = (lookerPayload) => {

  const dimensions = map(lookerPayload.fields.dimensions, 'name')

  const data = map(lookerPayload.data, (row) => {
    return {
      id: row[dimensions[0]].value,
      x: row[dimensions[1]].value,
      y: row[dimensions[2]].value
    }
  })

  const xValues = map(data, 'x')
  const yValues = map(data, 'y')

  const xDomain = [min(xValues), max(xValues)]
  const yDomain = [min(yValues), max(yValues)]

  return {
    data,
    xDomain,
    yDomain,
  }
}

export default lookerTransform
