import { map, reduce, min, max, get, uniq } from 'lodash'

const MERGED_DIMENSION_NAMES = 'MERGED_DIMENSION_NAMES'

const lookerTransform = (lookerPayload) => {
  const groupBy = lookerPayload.fields.dimensions[0].name                 // group by first dimension
  const groups = uniq(map(lookerPayload.data, (e) => e[groupBy].value))
  const identity = lookerPayload.fields.measures[0].name                  // horizontal value is first measure
  const value = lookerPayload.fields.measures[1].name                     // vertical value is second measure
  const valueFormat = lookerPayload.fields.measures[1].valueFormat
  const sizeField = get(lookerPayload.fields.measures[2], 'name')         // size is third measure if present

  let rows = map(lookerPayload.data, (row) => {
    return reduce(row, (result, value, key) => {
      result[key] = value.value
      return result
    }, {})
  })
  
  let size = null
  if ( sizeField ) {
    const sizeValues = map(rows, (row) => row[sizeField])
    size = {
      key: sizeField,
      values: [
        max(sizeValues), 
        min(sizeValues)
      ],
      sizes: [10, 50],
    }  
  } else {
    size = 10
  }

  const valueValues = map(rows, (row) => row[value])
  const minValue = min(valueValues)
  const maxValue = max(valueValues)
  const valueScale = {
    type: 'linear',
    min: minValue - ((maxValue - minValue) * .15),
    max: maxValue + ((maxValue - minValue) * .15),
    reverse: false,
  }

  rows = {
    data: rows,
    groups
  }


  return {
    groupBy,
    groups,
    identity,
    value,
    valueFormat,
    size,
    valueScale,
    data: rows
  }
}

export default lookerTransform
