import { map, keyBy, reduce } from 'lodash'

const lookerTransform = (lookerPayload) => {
  const dimensions = keyBy(lookerPayload.fields.dimensions, (dimension) => dimension.name)
  const measure = lookerPayload.fields.measures[0].name
  const data = map(lookerPayload.data, (row) => {
    return {
      id: map(dimensions, (dimension) => row[dimension.name].value).join(' - '),
      label: map(dimensions, (dimension) => row[dimension.name].value).join(' - '),
      value: row[measure].value
    }
  })

  let total = reduce(data, (result, row) => {
    return result + row.value
  }, 0)


  const columns = 20
  const rows = 30


  return {
    data,
    total,
    rows,
    columns
  }
}

export default lookerTransform
