import { map, reduce, keyBy } from 'lodash'

const MERGED_DIMENSION_NAMES = 'MERGED_DIMENSION_NAMES'

const lookerTransform = (lookerPayload) => {
  const indexBy = MERGED_DIMENSION_NAMES
  const dimensionLegend = lookerPayload.fields.dimensions.length == 1 ? lookerPayload.fields.dimensions[0].label_short : null
  const measureLegend = lookerPayload.fields.measures[0].field_group_variant

  const measureNames = keyBy(lookerPayload.fields.measures, (measure) => measure.name)
  
  const data = {
    data: map(lookerPayload.data, (row) => {
      return reduce(row, (result, value, key) => {
        if (measureNames[key]) {
          result[measureNames[key].label] = value.value
        } else {
          result[MERGED_DIMENSION_NAMES] = result[MERGED_DIMENSION_NAMES] ? `${result[MERGED_DIMENSION_NAMES]} - ${value.value}` : value.value
        }

        return result
      }, {})
    }),
    keys: map(lookerPayload.fields.measures, 'label')
  }

  return {
    indexBy,
    dimensionLegend,
    measureLegend,
    data
  }
}

export default lookerTransform
