import { map, reduce, keyBy } from 'lodash'


const lookerTransform = (lookerPayload) => {
  const payload = lookerPayload.sql !== undefined ? lookerPayload : lookerPayload.data.query_results
  const measureNames = keyBy(payload.fields.measures, (measure) => measure.name)

  const data = map(payload.data, (row) => {
    return reduce(row, (result, value, key) => {
      if (measureNames[key]) {
        result.value = value.value
      } else {
        result.id = result.value ? `${result.value} - ${value.value}` : value.value
      }

      return result
    }, {})
  })

  return {
    data
  }
}

export default lookerTransform
