import { find, reduce, map, min, max } from 'lodash'

const lookerTransform = (lookerPayload) => {
  const dateDimension = find(lookerPayload.fields.dimensions, (dimension) => dimension.type == 'date_date')
  const measure = lookerPayload.fields.measures[0]
 
  const data = map(lookerPayload.data, (row) => {
    return reduce(row, (result, value, key) => {
      if (key === dateDimension.name) {
        result.day = value.value
      } else if (key === measure.name) {
        result.value = value.value
      }
      return result
    }, {})
  })

  const from = min(map(data, 'day'))
  const to = max(map(data, 'day'))

  return {
    data,
    from,
    to
  }
}

export default lookerTransform