import { forEach, keys, uniq, values, keyBy, reduce, map } from 'lodash'

const lookerTransform = (lookerPayload) => {

  const measures = keyBy(lookerPayload.fields.measures, (measure) => measure.name)
  const dimensions = keyBy(lookerPayload.fields.dimensions, (dimension) => dimension.name)

  const data = map(lookerPayload.data, (row) => {
    return reduce(row, (result, value, key) => {
      result[key] = value.value
      return result
    }, {})
  })

  let variables = reduce(data, (result, value) => {
    forEach(keys(value), (key) => {
      if (measures[key]) {
        result[key] = {
          key: key,
          type: 'linear',
          min: 'auto',
          max: 'auto',
          ticksPosition: 'before',
          legend: measures[key].label,
          legendPosition: 'start',
          legendOffset: 20
        }
      } else if (result[key]) {
        result[key].values.push(value[key])     
        result[key].values = uniq(result[key].values)   
      } else {
        result[key] = {
          key: key,
          type: 'point',
          padding: 1,
          values: [ value[key] ],
          legend: dimensions[key].label,
          legendPosition: 'start',
          legendOffset: -20
        }

      }
    })
    return result
  }, {})

  variables = values(variables)

  return {
    data,
    variables,
  }
}

export default lookerTransform